/* 
font-family: 'Open Sans', sans-serif;
font-family: 'Oswald', sans-serif;
font-family: 'Playfair Display', serif;
font-family: 'Roboto', sans-serif; 
*/

body {
  margin: 0;
  font-family: "Playfair Display", serif;
  transition: background-color 0.3s, color 0.3s;
}

body.light-mode {
  background-color: #f5f5dc;
  color: #1a2f2f;
}

body.dark-mode {
  background-color: #1a2f2f;
  color: #f8f9fa !important;
}

body.dark-mode .navLinks {
  color: #f8f9fa;
}

body.dark-mode .navLinks:hover {
  color: #c9d7f8;
}

.social-media-list {
  position: relative;
  font-size: 22px;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.social-media-list li a {
  color: #f5d491;
}

.social-media-list li {
  position: relative;
  display: inline-block;
  height: 60px;
  width: 60px;
  margin: 10px 3px;
  line-height: 60px;
  border-radius: 50%;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border: 1px solid #343d46;
}

body.dark-mode .social-media-list li {
  border: 1px solid white;
}

.social-media-list li:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  line-height: 60px;
  border-radius: 50%;
  opacity: 0;
  box-shadow: 0 0 0 1px #fff;
  transition: all 0.5s ease-in-out;
}

.social-media-list li:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background-color: #c9d7f8;
}

.social-media-list li:hover:after {
  opacity: 1;
  transform: scale(1.12);
  transition-timing-function: cubic-bezier(0.37, 0.74, 0.15, 1.65);
}

.social-media-list li:hover a {
  color: #000;
}

.navLinks {
  text-decoration: none;
  list-style: none;
  color: inherit;
  font-family: "Playfair Display", serif;
  padding: 0.5rem;
  cursor: pointer;
  transition: color 0.3s;
}

.navLinks:hover {
  color: #c9d7f8;
}

.selectedNav {
  font-weight: bold;
  font-size: larger;
  color: #0a3d62;
}

body.dark-mode .selectedNav {
  color: rgb(245, 212, 145);
}

.toggle-icon-button {
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  color: inherit;
  transition: transform 0.3s;
}

.toggle-icon-button:hover {
  transform: scale(1.2);
}

.contactDiv {
  width: 75%;
  color: #f5d491;
  font-family: "Playfair Display", serif;
}

.contactForm {
  border: 1px solid white;
  padding: 10px;
  border-radius: 10px;
}

.input {
  width: 100%;
  background-color: #c9d7f8;
  border: none;
}

textarea {
  width: 100%;
  background-color: #c9d7f8;
  border: none;
}

form label {
  font-size: 20px;
  font-weight: bold;
}

body.light-mode .contactDiv {
  color: #343d46; /* Darker text for light mode */
}

body.light-mode .contactForm {
  border: 1px solid #343d46; /* Darker border for light mode */
  background-color: #f5f5dc; /* Lighter background for light mode */
}

body.light-mode .input,
body.light-mode textarea {
  background-color: #ffffff; /* White background for better readability */
  color: #1a2f2f; /* Dark text color */
  border: 1px solid #343d46; /* Darker border */
}

body.light-mode .submitButton {
  background-color: #343d46; /* Darker button for contrast */
  color: #f5f5dc; /* Light text on button */
}

body.light-mode .errorMessage,
body.light-mode .submissionStatus {
  color: #cc0000; /* Red color for error messages in light mode */
}

.hidden {
  display: none;
}

.logo {
  width: 20rem;
  height: 20rem;
  line-height: 5.5rem;
  border-radius: 100%;
  margin: auto;
}

.imageOfMe {
  border-radius: 100%;
  width: 20rem;
  height: 20rem;
}

.aboutMeHeader {
  margin: auto;
}

.content {
  border-style: solid;
  border-color: #f5f5dc;
  border-top-width: 2px;
  border-bottom-width: 2px;
  max-width: 100%;
  margin-bottom: 5px;
}

body.light-mode .content {
  border-color: #343d46;
}

.content .inner {
  padding: 3rem 2rem;
  max-height: 40rem;
  overflow: hidden;
}

.content .inner h2 {
  font-size: 50px;
  color: #f5d491;
  font-family: "Playfair Display", serif;
}

body.light-mode .content .inner h2 {
  color: #343d46;
}

.content .inner p {
  font-size: 20px;
  color: #f5d491;
}

body.light-mode .content .inner p {
  color: #343d46;
}

.content .inner > :last-child {
  margin-bottom: 0;
}

.content p {
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  line-height: 2;
  font-family: "Playfair Display", serif;
}

.aboutMeParagraph {
  width: 80%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #f5d491;
  font-size: 18px;
  font-family: "Playfair Display", serif;
}

body.light-mode .aboutMeParagraph {
  color: #343d46;
}

.pageContainer {
  position: relative;
  min-height: 95vh;
}

.contentWrapper {
  padding-bottom: 2.5rem;
}

.footerWrapper {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

.pageTitle {
  color: #f5d491;
}

.pageTitle h1 {
  font-size: 50px;
  font-family: "Playfair Display", serif;
}

.portfolioTitle {
  font-size: 2.5rem;
  font-family: "Playfair Display", serif;
  transition: color 0.3s;
}

body.light-mode .portfolioTitle {
  color: #343d46; /* Darker color for light mode */
}

body.dark-mode .portfolioTitle {
  color: #f5d491; /* Lighter color for dark mode */
}

.submitButton {
  background-color: #f5d491;
  border: none;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

.projectCard {
  margin: 20px auto;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  border: 1px solid #f5d491;
  background-color: #1a2f2f;
  color: #f5d491;
}

.projectCard:hover {
  transform: scale(1.05);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.projectImg {
  position: relative;
  overflow: hidden;
  border-radius: 8px 8px 0 0;
}

.projectImageID {
  width: 100%;
  height: auto;
  border-radius: 8px 8px 0 0;
}

.imageOverlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  color: #f5d491;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s;
}

.projectImg:hover .imageOverlay {
  opacity: 1;
}

.overlayLink {
  color: #f5d491;
  margin: 8px 0;
  font-weight: bold;
  text-decoration: none;
}

.overlayLink:hover {
  text-decoration: underline;
}

.projectDetails {
  padding: 15px;
  text-align: center;
}

.projectName {
  font-size: 1.5rem;
  color: #f5d491;
  margin-bottom: 8px;
}

.techStack,
.projectPurpose {
  font-size: 1rem;
  margin-bottom: 8px;
}

.deployedText {
  color: #f5d491;
}

.githubText {
  color: #f5d491;
}

.resumeSection {
  margin-left: 50px;
  margin-bottom: 50px;
  margin-top: 50px;
  color: #f5d491;
  font-family: "Playfair Display", serif;
}

.resumeSection p {
  font-size: 20px;
}

.resumeSection a {
  color: #f5d491;
}

body.light-mode .resumeSection a {
  color: #1a2f2f;
}

body.light-mode .resumeSection p {
  color: #1a2f2f;
}

.skillsContainer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  align-items: center;
}

.buttonContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.skillButton {
  background-color: #f5d491;
  color: #343d46;
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  flex: 1 1 150px;
  max-width: 200px;
}

.skillButton:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skillsSection {
  background: #1a2f2f;
  color: #f5d491;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  text-align: center;
  width: 100%;
  max-width: 600px;
}

.skillGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  padding: 1rem;
}

.skillItem {
  background-color: #1a2f2f;
  border: 1px solid #f5d491;
  padding: 1rem;
  border-radius: 12px;
  transition: transform 0.3s, box-shadow 0.3s;
  opacity: 0;
  animation: fadeInUp 1s ease forwards;
  width: 100%;
  max-width: 150px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.closeButton {
  background-color: #f5d491;
  color: #343d46;
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 8px;
  margin-top: 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
}

.closeButton:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

@keyframes fadeInUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.skillDelay0 {
  animation-delay: 0s;
}

.skillDelay1 {
  animation-delay: 0.1s;
}

.skillDelay2 {
  animation-delay: 0.2s;
}

.skillDelay3 {
  animation-delay: 0.3s;
}

.skillDelay4 {
  animation-delay: 0.4s;
}

body.light-mode .skillsSection,
body.light-mode .skillItem {
  background: #f5f5dc;
  border-color: #1a2f2f;
  color: #1a2f2f;
}

body.light-mode .skillButton {
  background-color: #343d46;
  color: #f5d491;
}

body.light-mode .skillButton:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

body.light-mode .closeButton {
  background-color: #343d46;
  color: #f5d491;
}

body.light-mode .closeButton:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

@media screen and (min-width: 768px) {
  .projectDiv:hover {
    box-shadow: 0 0 30px 15px #fff, 0 0 50px 30px #f0f, 0 0 70px 45px #0ff;
  }

  .navLinks {
    font-size: 20px;
  }
}

@media screen and (max-width: 767px) {
  .pageTitle h1 {
    font-size: 1.5rem;
    text-align: center;
  }

  .navLinks {
    font-size: 18px;
    padding: 0.75rem;
  }

  .menu-icon-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: inherit;
  }

  .toggle-icon-button {
    margin-left: 10px;
  }

  .headerClass {
    padding: 0.5rem;
  }

  .headerClass .row.align-items-center {
    display: flex;
    justify-content: space-between;
  }

  .skillsContainer {
    padding: 1rem;
  }

  .skillButton {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
    max-width: 150px;
  }

  .skillGrid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }

  .skillsSection {
    padding: 1rem;
  }

  .closeButton {
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
  }
}

/* Dark Icons for Light Mode */
.dark-icon {
  color: #1a2f2f !important;
}
